.profile__job {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
@media screen and (max-width: 930px) {
    .profile__job {
        text-align: center;
        font-size: 14px;
        line-height: 17px;
    }
}